import React from 'react';
import { animated, useSpring } from 'react-spring';

import '../styles/nav.css'

const Nav = ({ isOpen, toggleMenu }) => {
  const { x } = useSpring({
    x: isOpen ? 0 : 100
  });

  return (
    <React.Fragment>
      <animated.nav
        className="nav"
        style={{ transform: x.interpolate(x => `translateX(${x * -1}%)`) }}
      >
      </animated.nav>
      
      { isOpen && <div className="nav-backdrop" onClick={toggleMenu}></div> }
    </React.Fragment>
  );
};

export default Nav;
